<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process173"></a>

<p><br></p>
<p><span class="rvts17">燃烧的先知（</span><span class="rvts50">The Burning Prophet</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:150,Y:12</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：先知石像</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">对话先知</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1180.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">燃烧的先知位于浮木城西门一直走，过了矮人埋伏区域后的一个山顶处。尝试将先知石像下的火炬点着，但是每当点着一支后，另一支会立马灭掉。</span>
<br><span class="rvts21">除非手速足够快，不然的话几乎很难在同一时间内点着所有的火炬。使用</span><span class="rvts20">AOE</span><span class="rvts21">的火焰技能（火球，或油面点火）可以瞬间将五支火炬同时点着。</span>
<br><span class="rvts21">接着与先知对话，他会告诉你一段雷克斯战斗的往事（这先知就是因为老预告神灵的失败，才会得到这样的后果），之后他会奖励你一件普通装备，先知石像瞬间五彩斑斓，任务关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



